/* General styles */
body {
  font-family: 'Merriweather', serif;
  background-color: #fff;
  color: #000;
  margin: 0;
  padding: 0;
}

.mane h1, h2, h3, h4, h5, h6 {
  color: #000!important;
  margin-bottom: 20px;
}

a {
  color: #FF76CE;
  text-decoration: none;
}

a:hover {
  color: #A3D8FF;
  text-decoration: underline;
}

/* Container styles */
.container {
  background-color: #FDFFC2;
  border: 1px solid var(--border-color);
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 8px;
}

/* Part styles */
.part {
  margin-bottom: 20px;
  border-radius: 8px;
}
.part > div{
  page-break-inside: avoid;
}
/* Warming section styles */
.warming {
  background-color: #94FFD8;
  padding: 15px;
  margin-bottom: 20px;
  border-radius: 8px;
  font-weight: bold;
}

/* Bunch styles */
.bunch {
  background-color: #FF76CE;
  padding: 15px;
  margin-bottom: 20px;
  border-radius: 8px;
}

/* Hint styles */
.hint {
  background-color: #FDFFC2;
  color: #181a20;
  padding: 10px;
  margin-bottom: 15px;
  border-left: 5px solid #FF76CE;
  border-radius: 4px;
  font-style: italic;
}

/* Expandable styles */
.expandable {
  position: relative;
  display: inline-block;
  cursor: pointer;
}

.expandable .word {
  font-weight: bold;
  text-decoration: underline;
  cursor: pointer;
}

.expandable .definition {
  display: none;
  position: absolute;
  background-color: #FF76CE;
  color: #fff;
  padding: 10px;
  border-radius: 4px;
  z-index: 1;
  width: max-content;
  max-width: 300px;
  top: 100%;
  left: 50%;
  transform: translateX(-50%);
}

.expandable:hover .definition {
  display: block;
}

/* Styled lists */
ol, ul {
  margin-bottom: 20px;
}

li {
  margin: 1rem;
  font-size: 16px;
  line-height: 1.5;
}

li button {
  margin: 0 auto;
}

/* Table styles */
table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
  background-color: #A3D8FF;
  border-radius: 8px;
  overflow: hidden;
  box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
}

th, td {
  padding: 15px;
  text-align: left;
  border-bottom: 1px solid var(--border-color);
}

th {
  background-color: #FF76CE;
  color: #fff;
}

td {
  background-color: #FDFFC2;
}

tr:last-child td {
  border-bottom: none;
}

/* Additional styles */
button {
  background-color: var(--primary-bg);
  color: var(--primary-text);
  border: 2px solid var(--border-color);
  padding: 10px 20px;
  border-radius: 4px;
  cursor: pointer;
}

button:hover {
  background-color: var(--hover-bg);
}

button:active {
  background-color: var(--active-bg);
}

/* Styled Lists */
ol, ul {
  width: 100%;
  color: var(--lesson-text);
}
li > div {
  display: flex;
  width: 100%;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-evenly;
}
li > div > button{
  display: inline-block;
    min-width: 100px; /* Set a minimum width */
    width: auto; /* Allow the button to grow */
    max-width: 100%; /* Prevent it from exceeding the container width */
    cursor: pointer;
    text-align: center;
    box-sizing: border-box; /* Ensures padding is included in the width calculation */
    transition: background-color 0.3s ease, width 0.3s ease;

}
ol {
  
  padding-left: 2rem;
}

ul {
  list-style-type: disc;
  padding-left: 2rem;
}

/* Tables */
table {
  width: 100%;
  border-collapse: collapse;
  margin-bottom: 20px;
  background-color: var(--lesson-bg);
  color: var(--lesson-text);
  border-radius: 8px;
  overflow: hidden;
}

table, th, td {
  border: 1px solid var(--lesson-border);
}

th, td {
  padding: 15px;
  text-align: left;
}

th {
  background-color: var(--lesson-accent1);
  color: var(--lesson-text);
}

td {
  background-color: var(--lesson-accent2);
}

tr:nth-child(even) td {
  background-color: var(--lesson-accent3);
}


.lesson-page {
  width: 100%;
  display: grid;
  grid-template-columns: 5% auto 5%;
  grid-template-rows: auto;
  overflow: hidden;
  position: absolute;
  gap:10px;
  background-color: #f8f9fa;

}

.lesson-page .content {
  grid-column: 2 / 3; /* Main content area */
  overflow: auto; /* Allows scrolling if content overflows */
  padding: 20px; /* Adjust padding as needed */
  height: 100%;
  box-sizing: border-box;
}

.lesson-page .content > div {
  max-width: 100%;
  word-wrap: break-word;
  margin: 0;
}
  
  .lesson-image {
    width: 100%;
    height: auto;
  }
  
  .lesson-content {
    margin-top: 20px;
  }
  .lesson-overlay {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: rgba(0, 0, 0, 0.8); /* Semi-transparent background */
    color: white;
    padding: 20px;
    box-sizing: border-box;
    overflow: auto;
    z-index: 1000; /* Ensure it's on top of other content */
  }
  
  .lesson-overlay img {
    max-width: 100%;
    height: auto;
  }
  
  .lesson-overlay button {
    position: absolute;
    top: 10px;
    right: 10px;
    background-color: #f44336; /* Red color */
    border: none;
    color: white;
    padding: 10px 20px;
    cursor: pointer;
  }
  
  .lesson-overlay button:hover {
    background-color: #c62828; /* Darker red */
  }
  
  .correct {
    background-color: lightgreen !important;
  }
  .endpoint {
    max-width: 600px;
    margin: 50px auto;
    padding: 20px;
    background: linear-gradient(135deg, #f7f9fc, #e0e7ff);
    border-radius: 15px;
    box-shadow: 0 8px 20px rgba(0, 0, 0, 0.1);
    text-align: center;
}

.endpoint h2 {
    font-size: 28px;
    font-weight: bold;
    color: #333;
    margin-bottom: 20px;
    text-transform: uppercase;
    letter-spacing: 1.2px;
    border-bottom: 2px solid #e0e7ff;
    padding-bottom: 10px;
}

.endpoint p {
    font-size: 18px;
    color: #555;
    margin: 15px 0;
    line-height: 1.6;
}

.endpoint button {
    background: linear-gradient(135deg, #6a8dff, #4a6aff);
    color: #fff;
    font-size: 18px;
    font-weight: bold;
    padding: 12px 25px;
    border: none;
    border-radius: 8px;
    cursor: pointer;
    transition: background 0.3s ease, transform 0.2s ease;
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
    text-transform: uppercase;
    letter-spacing: 1px;
}

.endpoint button:hover {
    background: linear-gradient(135deg, #4a6aff, #2a4aff);
    transform: translateY(-2px);
    box-shadow: 0 6px 15px rgba(0, 0, 0, 0.15);
}

.endpoint button:active {
    transform: translateY(0);
    box-shadow: 0 4px 10px rgba(0, 0, 0, 0.1);
}

.single-column {
  column-count: 1;
}