/* Grid layout for the passage and questions */
.reading-use-of-english-grid {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 40px;
    padding: 40px;
    background-color: #f5f7fa;
    font-family: 'Helvetica Neue', Arial, sans-serif;
    line-height: 1.6;
  }
  
  /* Styling for the passage */
  .passage {
    background-color: #ffffff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow-y: auto;
    max-height: 80vh;
  }
  
  /* Styling for the questions */
  .questions {
    background-color: #ffffff;
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    overflow-y: auto;
    max-height: 80vh;
  }
  
  /* Individual question block styling */
  .question {
    margin-bottom: 30px;
  }
  
  .question p {
    font-size: 18px;
    margin-bottom: 15px;
  }
  
  /* Styling for buttons (answers) */
  .question button {
    display: block;
    width: 100%;
    padding: 10px 15px;
    margin-bottom: 10px;
    font-size: 16px;
    color: #333;
    background-color: #e9ecef;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .question button:hover {
    background-color: #ced4da;
  }
  
  .question button:disabled {
    background-color: #adb5bd;
    cursor: not-allowed;
  }
  
  /* Styling for the Submit button */
  .questions button {
    background-color: #007bff;
    color: #fff;
    font-size: 18px;
    padding: 12px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .questions button:hover {
    background-color: #0056b3;
  }
  
  /* Styling for the results section */
  .results {
    margin-top: 30px;
    padding: 20px;
    background-color: #e9ecef;
    border-radius: 8px;
    text-align: center;
  }
  
  .results p {
    font-size: 20px;
    font-weight: bold;
    color: #495057;
  }
  
  .results button {
    margin-top: 20px;
    padding: 10px 20px;
    font-size: 16px;
    background-color: #28a745;
    color: #fff;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    transition: background-color 0.3s ease;
  }
  
  .results button:hover {
    background-color: #218838;
  }
  