@import url('https://fonts.googleapis.com/css2?family=Merriweather:ital,wght@0,300;0,400;0,700;0,900;1,300;1,400;1,700;1,900&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Material+Symbols+Outlined:opsz,wght,FILL,GRAD@20..48,100..700,0..1,-50..200');

.profile-page { 
  background-color: #000000; 
  display: grid;
  grid-template-columns: 15vw auto;
  gap: 0px;
  margin: 0;
  padding: 0;
}
h1, h2, h3, h4, h5{
  color: white!important;
  margin: 0.2rem;
}
.sidebar { 
  position: sticky;
  top: 0;
  line-height: normal; 
  width: 100%; 
  height: 90vh;
  background-color: #000; 
  font-size: 16px; 
  font-weight: 400; 
  padding: 31px 0 80px; 
}
.sidebar-set{
  display: flex; 
  flex-direction: column; 
}
.info-item { 
  justify-content: center; 
  border-radius: 0px 20px 20px 0px; 
  box-shadow: 0px 4px 4px 0px #fff; 
  background-color: #fff; 
  display: flex; 
  gap: 10px; 
  color: #000; 
  padding: 5px 80px; 
}

.info-icon { 
  aspect-ratio: 1; 
  object-fit: auto; 
  object-position: center; 
  width: 25px; 
  fill: #000; 
}
.info-text { 
  font-family: Merriweather, sans-serif; 
}
.sidebar-items { 
  justify-content: start; 
  align-items: center; 
  display: flex; 
  margin-top: 15px; 
  flex-direction: row; 
  color: #fff; 
  padding: 0 30px; 
  gap: 1rem;
}

.sidebar-item { 
  display: flex; 
  gap: 10px; 
  margin-top: 24px; 
}

.sidebar-icon { 
  aspect-ratio: 1; 
  object-fit: auto; 
  object-position: center; 
  width: 25px; 
  fill: #fff; 
}
.sidebar-text { 
  font-family: Merriweather, sans-serif; 
}
.main-section { 
  display: flex; 
  flex-direction: column; 
  line-height: normal; 
  width: 100%; 
  margin: 0;
  border-left: 1px solid white;
  overflow: hidden; /* Ensure content is not overflowing */
}

.profile-section { 
  display: flex; 
  flex-grow: 1; 
  flex-direction: column; 
}

.profile-header { 
  justify-content: flex-end;  
  background-color: #000; 
  padding: 33px 0 33px 71px; 
}

.profile-info { 
  gap: 20px; 
  display: flex; 
}

.profile-image-container { 
  display: flex; 
  flex-direction: column; 
  line-height: normal; 
  width: 24%; 
  margin-left: 0px; 
}

.profile-image-wrapper { 
  justify-content: space-between; 
  display: flex; 
  margin-top: 14px; 
  flex-grow: 1; 
  gap: 10px; 
  font-weight: 400; 
}

.profile-image { 
  aspect-ratio: 1.43; 
  object-fit: auto; 
  object-position: center; 
  width: 100px; 
  max-width: 100%; 
}
.profile-name-container { 
  display: flex; 
  flex-direction: column; 
  margin: auto 0; 
}
.profile-label { 
  color: #fcd535; 
  font: 14px Merriweather, sans-serif; 
}
.profile-name { 
  color: #fff; 
  font: 20px Merriweather, sans-serif; 
}
.profile-details { 
  display: flex; 
  flex-direction: row; 
  flex-wrap: wrap;
  line-height: normal; 
  width: 90%; 
  margin-left: 20px; 
  align-content: flex-start; 
  flex-grow: 1; 
  padding-right: 15px; 
  font-weight: 400; 
  gap: 1.5rem;
}

.detail-item { 
  display: flex; 
  flex-direction: column; 
  width: 30%;
}

.detail-label { 
  color: #fcd535; 
  font: 14px Merriweather, sans-serif; 
}
.detail-value { 
  color: #fff; 
  font: 18px Merriweather, sans-serif; 
}
.start-section { 
  background-color: #f11a7b; 
  padding: 20px 15px; 
}

.start-content { 
  gap: 20px; 
  display: flex; 
}
.start-text { 
  display: flex; 
  flex-direction: column; 
  line-height: normal; 
  width: 20%; 
  margin-left: 0px; 
}
.start-heading { 
  color: #fff; 
  text-align: center; 
  text-shadow: 0px 2px 1.1px #fff; 
  align-self: stretch; 
  margin: auto 0; 
  font: 400 48px Merriweather, sans-serif; 
}

.start-steps { 
  display: flex; 
  flex-direction: column; 
  line-height: normal; 
  width: 68%; 
  margin-left: 20px; 
}

.steps-wrapper { 
  justify-content: center; 
  display: flex; 
  flex-grow: 1; 
  flex-direction: column; 
}

.steps-icons { 
  justify-content: space-between; 
  display: flex; 
  padding-right: 80px; 
  gap: 20px; 
}

.step-icon { 
  background-color: white;
  border-radius: 50% 0 50% 0;
  display: flex;
  align-items: center;
  justify-content: center;
  object-fit: auto; 
  object-position: center; 
  width: 24px; 
}
.steps-content{
  display: flex;
  flex-direction: row;
  flex-wrap: wrap; 
}
.step-item { 
  display: flex; 
  flex-direction: column; 
  line-height: normal; 
  width: 27%; 
  margin:0;
  padding: 1rem;
}

.step-box { 
  display: flex;
  flex-direction: column;
  justify-content: center; 
  border-radius: 20px; 
  box-shadow: 0px 4px 4px 0px #fff; 
  background-color: #fcd535; 
  flex-grow: 1; 
  color: #000; 
  text-align: center; 
  width: 100%; 
  padding: 0.4rem 0.4rem; 
  font: 400 16px Merriweather, sans-serif; 
}

.step-title { 
  color: #000; 
  align-self: center; 
  font: 700 20px Merriweather, sans-serif; 
}
.step-description { 
  justify-content: space-between; 
  display: flex; 
  gap: 3px; 
  font-size: 16px; 
  color: rgba(0, 0, 0, 0.81); 
  font-weight: 400; 
  padding: 11px 0; 
}
.step-img{
  width: 100%;
  margin: 0;
  padding: 0;
}
.step-text { 
  font-family: Merriweather, sans-serif; 
  margin: auto 0; 
}
.step-image { 
  aspect-ratio: 1.14; 
  object-fit: auto; 
  object-position: center; 
  width: 91px; 
}

a{
  text-decoration: none;  /* Removes underline */
  color: inherit;  /* Inherits the color from the parent */
  display: flex;  /* Makes the link behave like a block-level element */
  align-items: center;  
}
a:hover{
transform: scale(1.1);
color: crimson
}


/* General styles */

.balance-section {
  display: flex;
  flex-direction: row;
  flex: 0 0 auto;
  flex-wrap: wrap;
  gap: 1rem;
  align-items: center;
  padding: 33px 0 33px 71px;
  background: var(--primary-bg);
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}
.balance-section > div{
  border: 1px solid rgb(26 221 150 / 71%);
  border-radius: 8px;
}

.balance-box {
  padding: 20px;
  background: var(--primary-bg);
  color: var(--primary-text);
  border-radius: 10px;
  text-align: center;
  margin-bottom: 20px;
}

.balance-wrapper {
  display: flex;
  flex-direction: row;
  gap: 1rem;
  align-items: center;
}

.balance-title {
  font-size: 1.5rem;
  font-weight: bold;
  margin-bottom: 10px;
}

.balance-value {
  font-size: 1.25rem;
  margin-bottom: 20px;
}

.recharge-btn {
  background: #FF76CE; /* Stylish pink */
  color: var(--primary-text);
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  margin-bottom: 10px;
  transition: background 0.3s, transform 0.2s;
}

.recharge-btn:hover {
  background: #FF4C8B; /* Darker pink on hover */
  transform: scale(1.05);
}

.crypto-recharge {
  font-size: 0.875rem;
  color: var(--secondary-text);
}

.lesson-options {
  width: 100%;
  max-width: 500px;
  margin-bottom: 20px;
}

.options-list {
  display: flex;
  justify-content: space-around;
}

.option-item {
  background: #A3D8FF; /* Stylish light blue */
  color: var(--primary-text);
  border: none;
  padding: 10px 20px;
  border-radius: 5px;
  cursor: pointer;
  font-size: 1rem;
  transition: background 0.3s, transform 0.2s;
}

.option-item:hover {
  background: #89C9FF; /* Slightly darker blue on hover */
  transform: scale(1.05);
}

.next-lesson-box {
  width: 100%;
  padding: 0.5rem;

  background: #1add96; /* Stylish light green */
  color: var(--primary-text);
  border-radius: 10px;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
}

.next-lesson-text {
  text-align: center;
  font-size: 1.25rem;
  font-weight: bold;
}
.itemMain{
  padding: 20px;
  margin:0;
}
.wide{
  width: 100%!important
}